.selected-bar-card {
    height: 35px;
    display: -ms-flexbox;
    display: flex;
    border-top: 1px solid #000000;
    background-color: white;
}

.selected-bar-card:last-child {
    border-bottom: 1px solid #000000;
}

.selected-card-image-container {
    height: 100%;
    overflow: hidden;
    width: 80px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.selected-card-image-container > img {
    width: 120%;
    -webkit-transform: translateY(-20px) translateX(-10px);
        -ms-transform: translateY(-20px) translateX(-10px);
            transform: translateY(-20px) translateX(-10px);
}

.selected-card-faction-container {
    width: 25px;
    border-left: 1px solid black;
    border-right: 1px solid black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.selected-card-faction-container.blue {
    background-color: rgb(19, 113, 151);
}

.selected-card-faction-container.red {
    background-color: rgb(173, 26, 54);
}

.selected-card-faction-container.yellow {
    background-color: rgb(233, 196, 60);
}

.selected-card-faction-container.gray {
    background-color: rgb(159, 159, 153);
}

.selected-card-name-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding-left: 10px;
    font-weight: bold;
    font-size: 12px;
    width: 100%;
}

.selected-card-button {
    width: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    border-left: 1px solid black;
    font-weight: bold;
}

.selected-card-button.add-button {
    background-color: rgba(19, 113, 151, 0.15);
    cursor: pointer;
}

.selected-card-button.removal-button {
    background-color: rgba(173, 26, 54, 0.15);
    cursor: pointer;
}

.character-removal-button {
    width: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    border-left: 1px solid black;
    cursor: pointer;
    font-weight: bold;
    background-color: rgba(173, 26, 54, 0.15);
}


